.expense-date{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 15%;
    height: 90%;
    background-color: #000000;
    margin-left: 3mm;
    border-radius: 10px;
    border: 2px solid wheat;
    color: wheat;
}

#date-month{
    font-weight: 800;
    /* margin-bottom: 1mm; */

}

#date-date{
    font-size: 30px;
    font-weight: 800;

}

.expense-date p{
    margin: 0;
}

@media screen and (max-width:700px) {
    .expense-date{
        width: 20%;
    }

    #data-date{
        font-size: 20px;
        font-weight: 400;
    }

    #date-month{
        font-weight: 600;
    }

    #date-year{
        font-size: 12px;
    }
    
}