.expense-item{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    height: 3cm;
    margin: 5mm;
    background-color: #191A19;
    border-radius: 10px;
    border: 2px solid black;
    padding-right: 5mm;

}

.expense-title{
    font-weight: 700;
    font-size: 26px;
    color: wheat;
    /* justify-content: flex-start; */
}

.expense-price{
    font-size: 22px;
    padding:2mm;
    width: 2cm;
    display: flex;
    justify-content: center;
    background-color: #270082;
    color: wheat;
    border: 1px white solid;
    border-radius: 15px ;
}


@media screen and (max-width:700px) {

    .expense-item {
        width: 95%;
        margin: 1mm;
        padding: 0; 
        padding-top: 2mm;
    }

    .expense-title{
        font-size: 16px;
    }

    .expense-price{
        font-size: 16px;
        width: 1.5cm;
    }
}