.mainForm{
    display: flex;
    width: 50%;
    flex-direction: row;
    padding: 5mm;
    height: 200px;
    background-color: #6E85B2;
    margin-bottom: 5mm;
    border-radius: 10px;
}

.mainForm1{
    display: flex;
    width: 100%;
    flex-direction: row;
    /* padding: 5mm; */
    height: 100%;
    flex-wrap: wrap;
    border-radius: 10px;
    background-color: #6E85B2;
    /* : 400px; */
}

.form-items-label{
    width: 100%;
    font-size: 1.3rem;
    font-family: "Poppins",Arial, Helvetica, sans-serif;
    height: 7mm;
    border-radius: 10px;
    border: none;
    padding-left: 1mm;
    font-weight: 800;
    /* color: white; */
    /* ; */
}

.form-items-input{
    width: 100%;
    font-size: 1rem;
    font-family: "Poppins",Arial, Helvetica, sans-serif;
    height: 7mm;
    border-radius: 10px;
    border: none;
    padding-left: 1mm;
    /* font-weight: 800; */
}

.body-form{
    margin: 2mm;
    width: 45%;
    display: flex;
    flex-direction: column;
    height: 30%;
}

.submitBtn{
    width: 100%;
    height: 100%;
    background-color: #4C0070;
    border: none;
    border-radius: 10px;
    font-size: 7mm;
    color:wheat;
    font-weight: 600;
    font-family:"Poppins",sans-serif;
    cursor: pointer;
}
.submitBtn:hover{
    background-color: #2A0944;
}
.submitBtn:active{
    background-color: #082032;
}

@media screen and (max-width:700px) {
    .mainForm{
        width: 96%;
        padding: 0;
    }
    .submitBtn{
        font-size: 4mm;
    }
}