.expense{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 50%;
    /* height:700px; */
    padding: 5mm;
    background-color: #000000;
    box-shadow: 0px 0px 0px #000000,0px 0px 20px #255,0px 0px 80px #000000;
    border-radius: 10px;
}

.year-selection-box{
    width: 2.5cm;
    font-size: 6mm;
    font-weight:bold;

}


@media screen and (max-width:700px) {
    .expense{
        width: 98%;
        padding: 0mm;
    }   
}